/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #000000;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Open Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #003057;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #ffffff;
  font-size: 2rem;
}

/* Autor @Carlos Orellano Rondan - Orellano428@gmail.com
 https://github.com/Cali99-droid*/
/*Genericos*/
:root {
  --primario: #0024a8;
  --secundario: #7f7481;
}

/*Contenedor principal*/
.contenedor-principa--log {
  background-color: var(--primario);
  font-family: "Lato", sans-serif;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#0024a8+0,7db9e8+90,7db9e8+90&0.79+0,0+99 */
  background: -moz-linear-gradient(top, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#c90024a8", endColorstr="#007db9e8",GradientType=0 );
  /* IE6-9 */
}

.video video {
  display: block;
  width: 100%;
  margin-top: -20rem;
}

.contenido-video {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

/*Pantalla inicial*/
.principal {
  text-align: center;
  color: #fff;
  width: 100%;
  margin-top: 30rem;
  height: 50rem;
}

.img {
  width: 15%;
}

/* Full-width input fields */
.login-text {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

/* Set a style for all buttons */
.titulo--log {
  width: 100%;
}

.titulo--log button {
  background-color: #ff8800;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 20%;
}

.boton-acceder {
  background-color: #ff8800;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  border-radius: 8px;
  font-size: 2rem;
}

.boton-acceder:hover {
  opacity: 0.8;
}

/* Extra styles for the cancel button */
.cancelbtn {
  width: auto;
  padding: 10px 18px;
  background-color: #f44336;
}

/* Center the image and position the close button */
.imgcontainer {
  text-align: center;
  margin: 24px 0 12px 0;
  position: relative;
}

img.avatar {
  width: 10%;
  border-radius: 50%;
}

.container {
  padding: 16px;
}

span.psw {
  float: right;
  padding-top: 16px;
}

/* The Modal (background) */
.modal {
  /* Hidden by default */
  display: none;
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  /*height: 100%;  Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  padding-top: 60px;
}

/* Modal Content/Box */
.modal-content {
  background-color: #b4b4b4;
  margin: 2% auto 15% auto;
  /* 5% from the top, 15% from the bottom and centered */
  width: 40%;
  /* Could be more or less, depending on screen size */
  height: 80%;
}

/* The Close Button (x) */
.close {
  position: absolute;
  right: 25px;
  top: 0;
  color: #000;
  font-size: 35px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: red;
  cursor: pointer;
}

/* Add Zoom Animation */
.animate {
  -webkit-animation: animatezoom 0.6s;
  animation: animatezoom 0.6s;
}

@-webkit-keyframes animatezoom {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}
@keyframes animatezoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
/*FOTERR*/
.footer {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 23.3rem;
  background-color: #333333;
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 768px) {
  span.psw {
    display: block;
    float: none;
  }

  .cancelbtn {
    width: 100%;
  }

  .video {
    margin-top: 20rem;
    position: relative;
    height: 200rem;
    overflow: hidden;
  }

  .video video {
    display: block;
    width: 100%;
    margin-top: -3rem;
  }

  .principal {
    background-color: var(--primario);
    text-align: center;
    color: #fff;
    width: 100%;
    margin-top: 8rem;
    height: 0rem;
    font-size: 0.9rem;
  }

  body {
    background-color: var(--primario);
  }

  .titulo--log button {
    padding: 2rem;
    width: 100%;
  }
}
.alerta {
  padding: 1.25rem;
  color: #ffffff;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #71b100;
}

.contenido-dashboard {
  background-color: #fff;
  margin-top: 0.5rem;
}
.contenido-dashboard .tit-dash {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  background-color: #35547f;
}
.contenido-dashboard .tit-dash div {
  margin: 0 3rem 0 3rem;
}
.contenido-dashboard .contenido-dash {
  width: 100%;
  margin-top: 2rem;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(3, 1fr);
}
.contenido-dashboard .contenido-dash .dash {
  width: 40rem;
  height: 30rem;
  margin: 0 auto;
}

