}

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#0024a8+0,7db9e8+90,7db9e8+90&0.79+0,0+99 */
  background: -moz-linear-gradient(top, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(0, 36, 168, 0.79) 0%, rgba(125, 185, 232, 0.07) 90%, rgba(125, 185, 232, 0) 99%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#c90024a8", endColorstr="#007db9e8",GradientType=0 );
  /* IE6-9 */
}

.video video {
  display: block;
  width: 100%;
  margin-top: -20rem;
}

.contenido-video {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

/*Pantalla inicial*/
.principal {
  text-align: center;
  color: #fff;
  width: 100%;
  margin-top: 30rem;
  height: 50rem;
}

.img {
  width: 15%;
}

/* Full-width input fields */
.login-text {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

/* Set a style for all buttons */
.titulo--log {
  width: 100%;
}

.titulo--log button {
  background-color: #ff8800;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 20%;
}

