.datos-integrante .zoom {
  padding: 20px;
  background-color: #00b490;
  transition: transform 0.2s;
  width: 60%;
  margin: 1rem auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.datos-integrante .zoom:hover {
  transform: scale(1.3);
}
.datos-integrante .zoom p {
  margin: 0;
  color: #ffffff;
}
.datos-integrante p {
  margin: 0;
}
.datos-integrante .datos {
  width: 100%;
  height: 100%;
}
.datos-integrante .datos .avatar-int {
  width: 80%;
  height: 100%;
  margin: 0 auto;
}
.datos-integrante .datos--text .datos__nombre {
  font-weight: bold;
  font-size: 3rem;
}
.datos-integrante .datos--general {
  width: 80%;
}
.datos-integrante .detalles-integrante {
  display: flex;
  width: 100%;
  flex-direction: column;
  margin: 1rem auto;
  background-color: #ffffff;
}
.datos-integrante .detalles-integrante h3 {
  margin: 0;
}
.datos-integrante .detalles-integrante .hrh hr {
  margin-top: 5rem;
  width: 95%;
  background-color: #333333;
  height: 2px;
}
.datos-integrante .detalles-integrante .detalle {
  width: 95%;
}
.datos-integrante .detalles-integrante .detalle .contenedor-tabla__perfil {
  height: 27rem;
  padding-top: 0;
}
.datos-integrante .detalles-integrante .detalle .titulo_participacion {
  font-weight: 700;
  width: 95%;
  margin: 0 auto;
  padding: 0.1rem;
}

.contenedor-grupos .formulario-evento {
  padding: 2rem;
  width: 95%;
  margin: 0 auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
.contenedor-grupos .formulario-evento .entrada {
  display: flex;
  flex-direction: column;
}
.contenedor-grupos .formulario-evento .entrada button {
  background-color: #003057;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  cursor: pointer;
  display: block;
  border: none;
  width: 100%;
}
.contenedor-grupos .formulario-evento .entrada .org {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.contenedor-grupos .formulario-evento .entrada .botones-accion {
  width: 100%;
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: flex-end;
}
.contenedor-grupos .formulario-evento .entrada .botones-accion button {
  background-color: #003057;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  display: inline-block;
  border: none;
}
.contenedor-grupos .formulario-evento .entrada .botones-accion button:hover {
  background-color: #00223e;
  cursor: pointer;
}
.contenedor-grupos .formulario-evento input,
.contenedor-grupos .formulario-evento select {
  padding: 12px 20px;
}
.contenedor-grupos .formulario-evento label {
  font-weight: 700;
  margin: 8px 0;
}
.contenedor-grupos .evento-accion {
  display: flex;
  width: 100%;
  align-content: center;
  align-items: center;
}
.contenedor-grupos .evento-accion .boton-evento {
  margin-left: 2rem;
}
.contenedor-grupos .evento-accion .lista-eventos {
  width: 200px;
}

.modal-agregar .modal-eventos {
  width: 43%;
}

.modal-org {
  width: 50%;
}

/* Style tab links */
.tabs {
  margin-top: 2rem;
}

.tablink {
  background-color: #555;
  color: white;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  font-size: 17px;
  width: 25%;
}

.tablink:hover {
  background-color: #777;
}

/* Style the tab content (and add height:100% for full page content) */
.tabcontent {
  color: #000000;
  display: none;
  padding: 50px 20px;
  height: 100%;
}
.tabcontent h3 {
  font-weight: 700;
  margin: 0;
}

